html, body {
  width: 100%;
  height: 100%;
}

.App {
  width: 100%;
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

#root {
  width: 100%;
  height: 100%;
}

.button {
  -ms-flex: 20% 1;
      flex: 20% 1;
  text-align: center;
  border-width: 0;
  border-radius: 6px;
  background-color: black;
  color: green;
  font-size: 200%;
