  cursor: pointer;
  z-index: 10;
}

.button[disabled] {
  border: 3px solid red;
  color: red;
}

.App-controls-previous-button {
  margin-left: 29%;
  margin-right: 1%;
}

.App-controls-next-button  {
  margin-right: 29%;
  margin-left: 1%;
}


.App-controls-holder {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-positive: 1;
      flex-grow: 1;
  -ms-flex-negative: 1;
      flex-shrink: 1;
  -ms-flex-preferred-size: auto;
      flex-basis: auto;
  height: 50%;
}

.App-controls-holder div {
  height: 66%;
  width: 100%;
  -ms-flex-item-align: center;
      align-self: center;
  -ms-flex: 1 1;
      flex: 1 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  font-size: 190%;
}

@media only screen and (min-width: 700px) {
  .App-controls-holder div {
    font-size: 400%;
  }
}


